import Vue from 'vue'
import VueScroller from 'vue-scroller'
import { infoApi, contListApi } from '@/api/personalPage'
import avatar from '@/components/AVATAR/'
import HeaderTopFix from '@/components/HeaderTopFix/'
import AppHeaderBar from '@/components/AppHeaderBar/'
import imgShowCenter from '@/components/imgShowCenter/'
import layer from '@/components/layer/'
import { H5OpenApp, isWeixinBrowser } from '@/utils/openApp'
Vue.use(VueScroller)
export default {
  name: 'sharePersonal',
  components: {
    avatar,
    imgShowCenter,
    AppHeaderBar,
    layer,
    HeaderTopFix
  },
  data () {
    return {
      uid: '',
      ownInfo: {},
      isShowPage: false,
      gender: ['', '男', '女'],
      getListProps: {
        pageNum: 1
      },
      lists: [],
      isTopHeaderShow: false,
      windowHeight: '',
      isFull: false,
      isShow: false,
      openPage: 'personalPage',
      noDataText: '- Sky is the limit. -'
    }
  },
  created () {
    this.uid = this.$route.query.uid
    this.windowHeight = String(window.innerHeight)
    this.getOwnerInfo()
  },
  methods: {
    // 获取自己的信息
    getOwnerInfo () {
      const props = {
        targetUid: this.uid,
        account: this.uid
      }
      infoApi(props).then(res => {
        if (res.status == 200 && res.data.code == 2000) {
          Object.assign(this.ownInfo, res.data.data)
          this.isShowPage = true
        }
      })
    },
    checkDom () {
      // 兼容下拉刷新样式
      const el = this.$refs.scroller.$el
      const load = el.getElementsByClassName('loading-layer')[0]
      load.style.display = 'none'
      const box = el.getElementsByClassName('_v-content')[0]
      const lastChild = box.children[box.children.length - 1]
      const lastChildClassName = box.children[box.children.length - 1].getAttribute('class')

      if (lastChildClassName !== 'mark') {
        console.log('不存在的时候创建')
        let para = document.createElement('p')
        para.setAttribute('class', 'mark')
        let node = document.createTextNode('加载中...')
        para.appendChild(node)
        para.style.textAlign = 'center'
        para.style.fontSize = '0.4266666rem'
        box.appendChild(para)
      }
      if (this.isFull) {
        console.log('完毕')
        load.style.display = 'block'
        lastChild.style.display = 'none'
      }
    },
    // 获取文章列表信息
    getLists (num, done) {
      if (this.isFull) {
        return
      }
      contListApi({
        targetUid: this.uid,
        pageNum: num
      }).then(res => {
        if (res.status == 200 && res.data.code == 2000) {
          const data = res.data.data
          if (data.length == 0) {
            this.isFull = true
            this.checkDom()
            if (!this.lists.length) {
              this.noDataText = '他还没有发表内容呢'
            }
            done && done('finishInfinite')
            return
          }
          data.forEach(item => {
            const now = new Date()
            const publishDate = new Date(item.publishDate)
            const hour = publishDate.getHours() < 10 ? '0' + publishDate.getHours() : publishDate.getHours()
            const minute = publishDate.getMinutes() < 10 ? '0' + publishDate.getMinutes() : publishDate.getMinutes()
            const time = `${hour}:${minute}`
            const cntFav = Number(item.cntFav)
            const cntCommentStr = Number(item.cntCommentStr)
            const cntLikeStr = Number(item.cntLikeStr)
            let circle = null
            // let date = `${publishDate.getMonth() + 1}月${publishDate.getDate()}日`
            if (item.circleId) {
              circle = {
                circleId: item.circleId,
                circleIcon: item.circleIcon,
                circleName: item.circleName
              }
            }
            let date = {
              month: publishDate.getMonth() + 1,
              day: publishDate.getDate() < 10 ? '0' + publishDate.getDate() : publishDate.getDate(),
              neatest: false
            }
            let fullYear = publishDate.getFullYear()
            let year = fullYear !== now.getFullYear() ? fullYear : ''
            if (publishDate.toDateString() == now.toDateString()) {
              date.neatest = true
              date.str = '今天'
            }
            if (fullYear == now.getFullYear() && publishDate.getMonth() == now.getMonth() && publishDate.getDate() !== now.getDate()) {
              if ((now.getDate() - publishDate.getDate()) == 1) {
                date.neatest = true
                date.str = '昨天'
              }
            }
            let {eventCoverSrc, eventSubTitle, eventTitle, srcType, quality} = item
            const props = {
              time,
              year,
              date,
              cntFav,
              cntCommentStr,
              cntLikeStr,
              eventCoverSrc,
              eventSubTitle,
              eventTitle,
              srcType,
              quality,
              circle
            }

            console.log('propsprops', props)
            // 判断发布的东西是状态的时候
            if (item.contType == 0) {
              // 是图文状态的时候
              if (item.timelineType == 0) {
                console.log('图文', JSON.parse(item.content))
                const images = JSON.parse(item.content).images
                const multi = images.length > 3
                const imgs = multi ? images.slice(0, 4) : [JSON.parse(item.coverSrc)]
                const cont = JSON.parse(item.content).text
                Object.assign(props, {
                  type: 'images',
                  imgs,
                  cont,
                  long: images.length
                })
              }
              // 视频状态的时候
              if (item.timelineType == 1) {
                const imgs = [JSON.parse(item.coverSrc)]
                const cont = JSON.parse(item.content).text
                Object.assign(props, {
                  type: 'video',
                  imgs,
                  cont
                })
                console.log('视频', item.content)
              }
            }
            // 判断发布的东西是文章的时候
            if (item.contType == 1) {
              const title = item.title
              const imgs = [JSON.parse(item.coverSrc)]
              const cont = item.contAbstract
              // const srcType = item.srcType
              Object.assign(props, {
                type: 'article',
                title,
                imgs,
                cont
                // srcType
              })
              console.log('文章imgs', props)
            }
            this.lists.push(props)
          })
          done && done('finishInfinite')
        }
      })
    },
    openApp () {
      if (isWeixinBrowser()) {
        this.isShow = true
      } else {
        const adrPath = '?jt=2&uid=' + this.$route.query.uid
        const iosPath = '?jt=2&uid=' + this.$route.query.uid
        H5OpenApp({iosPath: iosPath, adrPath: adrPath})
      }
    },
    infinite (done) {
      this.checkDom()
      if (this.isFull) {
        done && done('finishInfinite')
        return
      }
      this.getLists(this.getListProps.pageNum++, done)
    },
    showMask (v) {
      this.isShow = true
    },
    closeMask (v) {
      this.isShow = false
    }
  },
  computed: {
    userFollowInfo () {
      const iconB = `<img style="width: 11px" src="${require('../../assets/B@3x.png')}">`
      const iconG = `<img style="width: 11px" src="${require('../../assets/G@3x.png')}">`
      const gender = ['', iconB, iconG][this.ownInfo.sex]
      // 1：白羊座，2：金牛座，3：双子座，4：巨蟹座，5：狮子座，6：处女座，7：天秤座，8：天蝎座，9：射手座，10：摩羯座，11：水瓶座，12：双鱼座
      const star = [
        '',
        '白羊座',
        '金牛座',
        '双子座',
        '巨蟹座',
        '狮子座',
        '处女座',
        '天枰座',
        '天蝎座',
        '射手座',
        '摩羯座',
        '水瓶座',
        '双鱼座'
      ][this.ownInfo.starSign]
      const regionStr = this.ownInfo.regionStr
      const arr = Array.from(new Set([gender, star, regionStr]))
      let result = []
      if (arr.length) {
        result = arr.filter(x => x !== '')
      }
      console.log('result', result)
      return result.length > 1 ? result.join(' | ') : result.join('')
    }
  }
}



