import { H5OpenApp, isWeixinBrowser } from '@/utils/openApp'
// import mixins from '../../utils/shareOpenMethod'
export default {
  name: 'HeaderTopFix',
  // mixins: [mixins],
  props: {
    text: {
      type: [String, Number],
      default: '浏览器'
    },
    openPage: {
      type: String,
      default: null
    },
    isVideo: {
      type: Boolean,
      default: false
    },
    contId: {
      type: String,
      default: ''
    },
    luckyId: {
      type: String,
      default: ''
    },
    eventId: {
      type: String,
      default: ''
    },
    uid: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isShowMask: false
    }
  },
  methods: {
    closeMask () {
      this.isShowMask = !this.isShowMask
    },
    open () {
      if (isWeixinBrowser()) {
        this.isShowMask = true
        return
      }
      let adrPath = ''
      let iosPath = ''
      if (this.openPage == 'detail') {
        const _href = window.location.href
        const reg = /\?from=[^&]*&isappinstalled=[0|1]/
        let href = _href.replace(reg, '')
        href = href.replace('sharedetail', 'detail')
        adrPath = '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString() // 安卓没识别出#/detail，传值到.html, #/detail由安卓自己拼接
        iosPath = (this.isVideo) ? '?jt=5&contId=' + this.contId : '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString()
      }
      if (this.openPage == 'personalPage') {
        adrPath = '?jt=2&uid=' + this.uid
        iosPath = '?jt=2&uid=' + this.uid
      }
      if (this.openPage == 'lottery') {
        adrPath = '?jt=4&luckyId=' + this.luckyId
        iosPath = '?jt=4&luckyId=' + this.luckyId
      }
      if (this.openPage == 'event') {
        adrPath = '?jt=3&eventId=' + this.eventId
        iosPath = '?jt=3&eventId=' + this.eventId
      }

      H5OpenApp({iosPath: iosPath, adrPath: adrPath})
    }
  }
}



